import React, {useEffect, useState} from 'react'
import { Link } from 'react-router-dom'
//ìŠ¤ì™€ì´í¼ë¥¼ ì‚¬ìš©í•˜ê¸° ìœ„í•œ ì„í¬íŠ¸
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';

import {Navigation} from 'swiper/modules';

const VideoSlider = ({ videos, title, id }) => {
  //loadingìƒíƒœì— trueê°’ì„ í• ë‹¹
  const [loading, setLoading] = useState(true);

  //ì»´í¬ë„ŒíŠ¸ê°€ ë§ˆìš´íŠ¸ë˜ë©´ setLoadingí•¨ìˆ˜ë¥¼ í˜¸ì¶œí•˜ì—¬ loadingê°’ì„ falseìƒíƒœë¡œ ì—…ë°ì´íŠ¸
  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 500)
  }, []);
  //loadingì´ trueë©´ isLoadingì„ falseë©´ isLoadedë¥¼ ë°˜í™˜
  const youtubeClass = loading ? 'isLoading' : 'isLoaded';

  return (
    //youtubeClassë³€ìˆ˜ ê°’ì„ í´ë˜ìŠ¤ëª…ìœ¼ë¡œ ì ìš©
    <section id={id} className={youtubeClass}>
      <h2>{title}</h2>
      <div className='video__slider'>
        <Swiper
          slidesPerView={1}
          spaceBetween={20}
          navigation={true}
          modules={[Navigation]}
          className={`mySwiper-${id}`}
          breakpoints={{
            640: {
              slidesPerView:2,
              spaceBetween:20,
            },
            768: {
              slidesPerView:3,
              spaceBetween:20,
            },
            1024: {
              slidesPerView:4,
              spaceBetween:20,
            },
            1600: {
              slidesPerView:4,
              spaceBetween:20,
            },
          }}
        >
        {videos.map((video, key) => (
          <SwiperSlide key={key}>
            <div className="video">
              <div className="video__thumb play__icon">
                <Link to={`/video/${video.videoId}`}>
                  <img src={video.img} alt={video.title} />
                </Link>
              </div>
            </div>
          </SwiperSlide>
        ))}
        </Swiper>
      </div>
    </section>
  )
}

export default VideoSlider